img {
  max-width: 40%;
  height: auto;
}

li {
  list-style: none;
}

.js-cat {
  gap: 20px;
  display: flex;
}

.cat-info {
  margin-top: 30px;
}

.js-cat span {
  font-weight: 900;
}

.loader {
  color: #000;
  margin-top: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "Loading data, please wait...";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 100%;
  background: linear-gradient(#ff3d00 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

